body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,textarea,p,blockquote,th,td,input,select,textarea,button,article {margin:0;padding:0;font-family:'Malgun Gothic',sans-serif;line-height:1;-webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box;font-size: 14px;line-height: 1;letter-spacing: -.05em;}
.App{max-width: 1280px;margin:0 auto;padding:100px 0;}
.flex-box{display: flex;margin-bottom:30px;}
.flex-block{flex: 0 1 auto;}
.caution-block{padding: 10px 15px;background-color: #e9e9e9;color:#666;}.caution-block span{display:block;line-height: 24px;}
.caution-text{padding-top:15px;color:rgb(221, 35, 35)}
.list-title{font-size: 16px; font-weight: 700;}
.goods-list-box{padding:15px 25px; border:1px solid #ececec;border-radius: 15px; background-color: #f3f7fa;}
.goods-list-box .flex-box{align-items: center; padding-bottom: 15px; border-bottom: 1px solid #ccc;}
.goods-list-box .list-title{width: 100px;}
.goods-list-box .list-contents{width:calc(100% - 120px);margin-left: 20px;}
.goods-list-box .list-contents .MuiBox-root{display: flex;flex-direction: row;align-items: center;}
.goods-list-box .list-contents .MuiFormControl-root{flex: 0 1 auto;margin-left: 10px;}
.goods-list-box .list-contents .MuiFormControl-root:first-child{margin-left: 0;}
.goods-list-box .MuiOutlinedInput-root{background-color: #fff;}
.search-block,.goods-price{display:flex;flex-direction: row;}
.search-block .flex-block{margin-left: 10px;}
.search-block .MuiSelectUnstyled-root{min-width: 207px;}
.search-block .flex-block:first-child {margin-left: 0;}
.search-block textarea{min-width: 500px; height: 100px;resize: none;  border: 1px solid #ccc;border-radius: 5px;} 
.goods-price{align-items: center;}
.goods-price .flex-block{min-width: 200px; text-align: right;}
.goods-price .flex-block:nth-child(2){min-width: auto;margin:0 15px;}
.datepicker-block .MuiToggleButtonGroup-root{background-color: #fff;}
.datepicker-block .MuiButtonBase-root.MuiToggleButton-root.Mui-selected {background-color: #1976d2;color:#fff}
.datepicker-block .MuiButtonBase-root {padding:14px;}
.submit-button-box {justify-content: center;}
.submit-button-box .MuiButton-root{padding:15px 30px;font-size: 16px;}
.table-button-list{margin-top: 40px; align-items: center;}
.table-button-list .MuiButton-root {margin-left: 10px;}
.table-button-list .table-select-list {display: flex; margin-left: auto; align-items: center;}
.table-button-list .MuiInputBase-root{width: 150px; margin-left: 10px;}
.table-button-list .MuiInputBase-input.MuiOutlinedInput-input.MuiSelect-select {padding: 0;    line-height: 35px; text-indent: 10px;}
.table-footer-button {justify-content: end; margin-top: 15px;}
.table-footer-button button {margin-left: 10px;}
.goods-pagination .MuiPagination-ul{justify-content: center;}